.container {
  padding: 0 16px;
  margin: 0 auto;
  width: 100%;
  max-width: 1184px;
  box-sizing: border-box;
}

@media (min-width: 768px) {
  .container {
    padding: 0 24px;
  }
}

@media (min-width: 1024px) {
  .container {
    padding: 0 48px;
  }
}

@media (min-width: 1280px) {
  .container {
    padding: 0;
  }
}
